
.student-terms-container {
  min-height: 100vh;
  background: radial-gradient(circle at top right, #f0f4ff 0%, #f8f9fa 100%);
  font-family: 'Cairo', sans-serif;
}

.terms-header {
  background: linear-gradient(135deg, #1a237e 0%, #3949ab 100%);
  padding: 40px 20px;
  color: white;
  position: relative;
  overflow: hidden;
  box-shadow: 0 10px 30px rgba(26, 35, 126, 0.2);
}

.terms-header::before {
  content: '';
  position: absolute;
  top: -50%;
  right: -10%;
  width: 300px;
  height: 300px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 50%;
}

.terms-header-content {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  z-index: 1;
}

.brand-section h1 {
  font-size: 2.5rem;
  margin: 0;
  font-weight: 800;
  letter-spacing: -1px;
  display: flex;
  align-items: center;
  gap: 15px;
}

.user-profile {
  display: flex;
  align-items: center;
  gap: 20px;
  background: rgba(255, 255, 255, 0.1);
  padding: 8px 20px;
  border-radius: 50px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.user-meta .welcome-text {
  font-size: 0.8rem;
  opacity: 0.8;
  display: block;
}

.user-meta .name-text {
  font-weight: 600;
  font-size: 1.1rem;
}

.logout-btn {
  background: #ff5252;
  color: white;
  border: none;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.logout-btn:hover {
  transform: scale(1.1) rotate(10deg);
  background: #ff1744;
  box-shadow: 0 5px 15px rgba(255, 23, 68, 0.4);
}

.terms-main {
  max-width: 1200px;
  margin: -40px auto 40px;
  padding: 0 20px;
  position: relative;
  z-index: 2;
}

.intro-card {
  background: white;
  padding: 40px;
  border-radius: 24px;
  box-shadow: 0 20px 40px rgba(0,0,0,0.05);
  text-align: center;
  margin-bottom: 40px;
}

.intro-card h2 {
  font-size: 2.2rem;
  color: #1a237e;
  margin-bottom: 12px;
  font-weight: 700;
}

.intro-card p {
  color: #666;
  font-size: 1.1rem;
}

.terms-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 30px;
}

.term-glass-card {
  background: rgba(255, 255, 255, 0.9);
  border-radius: 24px;
  padding: 40px;
  text-align: center;
  transition: all 0.4s cubic-bezier(0.165, 0.84, 0.44, 1);
  cursor: pointer;
  border: 1px solid rgba(255, 255, 255, 0.4);
  position: relative;
  overflow: hidden;
  box-shadow: 0 10px 20px rgba(0,0,0,0.03);
}

.term-glass-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 6px;
  background: linear-gradient(90deg, #1a237e, #3949ab);
  opacity: 0;
  transition: opacity 0.3s;
}

.term-glass-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 30px 60px rgba(26, 35, 126, 0.15);
  border-color: #3949ab;
}

.term-glass-card:hover::before {
  opacity: 1;
}

.term-icon-wrapper {
  width: 100px;
  height: 100px;
  background: #f0f2ff;
  border-radius: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 25px;
  color: #1a237e;
  transition: all 0.3s ease;
}

.term-glass-card:hover .term-icon-wrapper {
  background: #1a237e;
  color: white;
  transform: scale(1.1) rotate(5deg);
}

.term-name {
  font-size: 1.8rem;
  font-weight: 700;
  color: #2c3e50;
  margin-bottom: 15px;
}

.term-badge {
  display: inline-block;
  padding: 6px 16px;
  background: #e8eaf6;
  color: #3f51b5;
  border-radius: 50px;
  font-size: 0.9rem;
  font-weight: 600;
  margin-bottom: 25px;
}

.action-trigger {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  color: #3949ab;
  font-weight: 700;
  font-size: 1.1rem;
  transition: gap 0.3s ease;
}

.term-glass-card:hover .action-trigger {
  gap: 20px;
}

.empty-terms-state {
  text-align: center;
  padding: 80px 20px;
  background: white;
  border-radius: 30px;
  box-shadow: 0 10px 30px rgba(0,0,0,0.05);
}

.empty-terms-state h3 {
  font-size: 1.8rem;
  color: #2c3e50;
  margin: 20px 0 10px;
}

@media (max-width: 768px) {
  .terms-header-content {
    flex-direction: column;
    gap: 25px;
    text-align: center;
  }
  
  .brand-section h1 {
    font-size: 2rem;
  }
  
  .intro-card {
    padding: 30px 20px;
  }
  
  .intro-card h2 {
    font-size: 1.8rem;
  }
}
